@mixin button {
  min-width: 100px;
  max-width: 100px;
  min-height: 50px;
  max-height: 50px;
  flex: 1;
  margin: 0;
  font-size: $font_size_small;
  color: $color_white;
  cursor: pointer;
  background-color: $button_color;
  border: 0;
  border-radius: $border_radius_button;
  outline: none;
  box-shadow: $box_shadow_main;

  &:hover {
    transform: scale(1.1);
  }

  @include breakpoint(medium) {
    min-width: 75px;
    max-width: 75px;
    min-height: 50px;
    max-height: 50px;
    font-size: $font_size_small;
  }

  @include breakpoint(large) {
    min-height: 50px;
    max-height: 50px;
    font-size: 1em;
  }

  @include breakpoint(wide) {
    min-height: 50px;
    max-height: 50px;
    font-size: $font_size_small;
  }
}
